import React, {Component} from "react";
import {PriceBox} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/PriceBox";
import {ClassificationAttributesBox} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/ClassificationAttributesBox";
import {DetailsButtons} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/DetailsButtons";
import {Buttons} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/Buttons";
import {Sticker} from "eshop/modules/configurator/components/lp/offers/Sticker";
import OnlineUtils from "eshop/utils/OnlineUtils";

export class FakeOfferBox extends Component {

    constructor(props) {
        super(props);
        this.selectOfferNoPhoneAction = this.selectOfferNoPhoneAction.bind(this);
        this.pickDeviceAction = this.pickDeviceAction.bind(this)
        this.props.selectOffer(this.props.proposition.id);
    }

    selectOfferNoPhoneAction(e) {
        e && e.preventDefault();
        this.props.selectOfferNoPhone(this.props.proposition.id)
    }

    pickDeviceAction(e)  {
        e && e.preventDefault();
        this.props.pickDevice(this.props.proposition.id, this.props.deviceListAddress);
    }

    render() {
        var proposition = this.props.proposition;
        var labels = {
            selectOffer: this.props.descriptions['select.offer.button.label.for.ASSIGNMENT'] || 'Przekaż usługę',
            pickDevice: this.props.selectButtonLabel,
            "details.open" : this.props.detailsLabel,
            "details.close" : getLabelsForDetails(this.props.descriptions)['close']
        };

        return(
            <div className="l-col l-col-xsmall-12 l-col-small-6 l-col-medium-4 l-col-3 l-col-xlarge-3  opl-carousel__item u-padding-l u-padding-top-l u-xsmall-no-padding-l u-xsmall-no-padding-r u-xsmall-transform-scale-09 u-xsmall-transform-scale-1-slick">
                <div className="g-white1-bg u-box-shadow u-padding-all-l u-large-padding-all-l-xl">
                    <Sticker sticker={proposition.sticker} renderMobileVersion={false} />
                    <span>
                        <p className='h3'>{this.props.propositionTitle}</p>
                    </span>
                    <div className="o-separator"/>
                    <span>
                        <p className='u-font-bold' dangerouslySetInnerHTML={{__html: this.props.proposition.longDescription}}></p>
                    </span>
                    {OnlineUtils.isAssignment(this.props.processType) &&
                        <div>
                            <div className="o-separator"/>
                            <Buttons
                                selectOfferNoPhoneAction={this.selectOfferNoPhoneAction}
                                pickDeviceAction={this.pickDeviceAction}
                                labels={labels}
                                disabled={this.props.disableAddToCart}
                                pickDeviceShouldBeHidden={true}
                            />
                        </div>
                    }
                </div>
            </div>
        )
    }
}

const getLabelsForDetails = (desc) => ({
    close  : desc['details.close']  || "def:Zamknij",
    header : desc['details.header'] || "def:Szczegóły oferty",
    select  : desc['details.select'] || "def:Wybieram ofertę bez telefonu"
});

const getFeaturesConfig = (desc) => ({
    dataBarDescription : desc['features.dataBarDescription'] || "def:Internet w telefonie"
});

